  margin-bottom: 0.8rem;
}

p:last-child {
  margin-bottom: 0;
}

a {
  color: #d13936;
  text-decoration: none;
  transition: all 0.1s ease-in-out;
  border-bottom: 1px solid transparent;
}

a:active, a:focus, a:hover {
  color: #ad2a28;
  border-bottom-color: #ad2a28;
}

blockquote {
  position: relative;
  padding: 0;
  margin: 0;
  font-style: italic;
}

@media only screen and (min-width: 992px) {
  blockquote::before {
    content: "";
    position: absolute;
    bottom: 100%;
    right: calc(100% + 0.4rem);
    font-size: 4.76837rem;
  }
}

blockquote p {
  margin: 0 0 0.8rem;
}

blockquote p:last-of-type {
  margin-bottom: 0.8rem;
}

blockquote + .source,
blockquote + .zdroj {
  text-align: right;
}

dt {
