}

#svg-icon-lib {
  display: none;
  pointer-events: none;
}

.icon-svg {
  display: inline-block;
  vertical-align: middle;
  width: 1.6rem;
  height: 1.6rem;
  pointer-events: none;
  color: currentColor;
}

.icon-svg svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: currentColor;
}

img,
svg {
  max-width: 100%;
}

#loading-screen {
  display: none;
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  background-color: #222;
  color: #fce4e3;
  text-transform: lowercase;
  z-index: 9999999;
  will-change: opacity;
  opacity: 0;
  transition: opacity 0.62s 0.62s cubic-bezier(0.62, 0, 0.8, 0.38);
}

.loading #loading-screen {
  pointer-events: initial;
  opacity: 1;
}

.loading #loading-screen h1 {
  opacity: 1;
}

.loading #loading-screen svg {
  opacity: 1;
  transform: scale3d(1, 1, 1);
}

#loading-screen h1,
#loading-screen svg {
  color: #fce4e3;
  display: block;
}

#loading-screen svg {
  margin: 0 auto;
  width: 8rem;
  height: 8rem;
  fill: currentColor;
}

#loading-screen h1 {
  margin-bottom: 3.2rem;
}

.loading-screen-content {
  text-align: center;
  opacity: 0;
  will-change: opacity, transform;
  transform: scale3d(1.2, 1.2, 1.2);
  transition: opacity 0.38s 0.38s cubic-bezier(0.62, 0, 0.8, 0.38), transform 0.38s 0.38s cubic-bezier(0.62, 0, 0.8, 0.38);
}

.loading .loading-screen-content {
  opacity: 1;
  transform: scale3d(1, 1, 1);
}

#header {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  min-height: 3.2rem;
  line-height: 3.2rem;
  background-color: #fdf2f2;
  padding: 0 1.6rem;
  color: #a77977;
  display: flex;
  will-change: transform;
  transition: transform 0.3s ease-in-out;
  transform: translate3d(0, 0, 0);
}

#header.is-hidden {
  transform: translate3d(0, -100%, 0);
}

#header .icon-svg {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.header-left {
  flex: 0 0 auto;
  padding-left: 2.8rem;
}

.header-left .icon-svg {
  margin-top: -2px;
  left: 1.2rem;
  width: 40px;
  height: 40px;
}

.header-center {
  flex: 1 auto;
  position: relative;
}

.header-right {
  margin-left: auto;
}

/* Horizontln navigace */
#navigation {
  z-index: 99999;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  min-height: 3.2rem;
  background-color: #1c1312;
  padding: 0;
  color: #fce4e3;
  text-align: center;
  line-height: 3.2rem;
  border-top: 1px solid rgba(211, 211, 211, 0.2);
  display: flex;
}

.nav-button {
  flex: 0 1 auto;
  position: relative;
  padding: 0 1.6rem;
  cursor: pointer;
  color: #fce4e3;
  border-bottom: 0;
  text-transform: uppercase;
  font-size: 0.8rem;
}

.nav-button:hover span {
  color: #fce4e3;
}

.nav-button.is-hidden {
  pointer-events: none;
  visibility: hidden;
}

.nav-button span {
  color: #a77977;
  will-change: color;
  transition: color 0.1s ease-in-out;
}

.nav-button .icon-svg {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 1.6rem;
  height: 1.6rem;
  color: #fce4e3;
}

.nav-center {
  flex: 1;
}

.nav-button-prev {
  border-right: 1px solid rgba(211, 211, 211, 0.2);
  text-align: left;
  padding-left: 3.2rem;
}

.nav-button-prev .icon-svg {
  left: 0.4rem;
  transform: rotate(180deg) translateY(50%);
}

.nav-button-next {
  border-left: 1px solid rgba(211, 211, 211, 0.2);
  text-align: right;
  padding-right: 3.2rem;
}

.nav-button-next .icon-svg {
  right: 0.4rem;
}

.slides {
  width: 100vw;
  height: 100%;
}

.slide {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: calc(100% - 3.2rem);
  padding: 4.8rem 0 0;
  transform: translate3d(0, 0, 0);
  will-change: transform, width;
  transition: transform .38s, width .38s;
  overflow-x: hidden;
  overflow-y: scroll;
  text-align: center;
