  bottom: 25%;
  left: 50%;
  transform: translateX(-50%);
  width: 130px;
  font-size: 0.8rem;
  line-height: 0.8rem;
  padding: 0.4rem;
  border: 1px solid #1c1312;
  border-radius: 3px;
  background-color: rgba(28, 19, 18, 0.9);
  text-align: center;
}

.radial-menu-item {
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid rgba(28, 19, 18, 0.5);
  background-color: #fce4e3;
  color: #1c1312;
  cursor: pointer;
  line-height: 30px;
  text-align: center;
  display: none;
}

.radial-menu-item.is-active {
  display: block;
}

.radial-menu-item:hover {
  background-color: #1c1312;
  color: #fce4e3;
  border-color: rgba(252, 228, 227, 0.5);
}

.radial-menu-item .icon-svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 20px;
  height: 20px;
