.sortable {
  counter-reset: section;
}

.sortable.is-dragged {
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.sortable img {
  user-drag: none;
  user-select: none;
  pointer-events: none;
}

.sortable-item {
  flex: 1;
}

.sortable-drag-item {
  position: relative;
  cursor: -moz-grab;
  cursor: -webkit-grab;
  cursor: grab;
  transition: transform 0.1s cubic-bezier(0.2, 0.62, 0.38, 1);
}

.sortable-drag-item.is-dragged, .sortable-drag-item:hover {
  transform: scale(1.038);
}

.sortable-drag-item.is-dragged {
  position: absolute;
  z-index: 1;
  cursor: -moz-grabbing !important;
  cursor: -webkit-grabbing !important;
  cursor: grabbing !important;
