  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.sortable-drag-item * {
  pointer-events: none;
}

.sortable-drop-item::before {
  counter-increment: section;
  content: counter(section) ". ";
  position: absolute;
  top: -0.8rem;
  left: 0.4rem;
  color: rgba(252, 228, 227, 0.62);
}

.is-dragged .sortable-drop-item {
  transform: scale(0.9);
