.faksimile-container {
  position: relative;
  flex: 0 1 38%;
  overflow-x: hidden;
  overflow-y: scroll;
  margin-left: 1.6rem;
  box-shadow: 0 0 3.2rem rgba(28, 19, 18, 0.8);
  border-radius: 3px;
  counter-reset: faksimile;
}

.faksimile-container.faksimile-single .faksimile-item::before {
  display: none;
}

.faksimile-container.faksimile-multiple {
  max-height: 90vh;
}

.active-slide .faksimile-container {
  min-width: 38%;
}

.half-slide-previous.previous-to-active-slide .faksimile-container {
  display: none;
}

.faksimile-item {
  position: relative;
}

.faksimile-item::before {
  counter-increment: faksimile;
  content: counter(faksimile);
  position: absolute;
  top: 1.6rem;
  left: 1.6rem;
  width: 1.6rem;
  height: 1.6rem;
  line-height: 1.6rem;
  background-color: rgba(28, 19, 18, 0.62);
  border-radius: 50%;
  box-shadow: 0 0 0.8rem rgba(28, 19, 18, 0.62);
  color: #fce4e3;
  font-family: "ff-meta-web-pro", Helvetica, Verdana, sans-serif;
  font-feature-settings: "lnum";
  font-size: 0.8rem;
  will-change: opacity;
  transition: opacity 0.1s ease-in-out;
}

.faksimile-item:hover::before {
  opacity: .38;
}

.faksimile-item img {
  width: 100%;
}

.text-red, .node-text-red {
  background-color: red;
  color: #fce4e3;
}

.text-blue, .node-text-blue {
  background-color: lightskyblue;
  color: #1c1312;
}

.text-orange, .node-text-orange {
  background-color: orange;
  color: #fdf2f2;
}

.text-gray, .node-text-gray {
  background-color: gray;
  color: #fce4e3;
}

.text-yellow, .node-text-yellow {
  background-color: yellow;
  color: #1c1312;
}

.editor-container {
  flex: 1 1 62%;
  padding: 0 1.6rem;
}

.active-slide .faksimile-container + .editor-container {
  flex: 0 1 62%;
  max-width: 60%;
}

.editor {
  margin: 0 auto;
}

.editor,
.editor h1,
.editor h2,
.editor h3 {
  font-family: "ff-meta-serif-web-pro", "Times New Roman", serif;
}

.editor h2 {
  line-height: 2rem;
  margin-bottom: 1.6rem;
}

.editor p {
  margin-bottom: 0.8rem;
}

.editor .canvas {
  background: #fce4e3;
  min-height: 500px;
  line-height: 1.6rem;
  text-align: left;
  padding: 3.2rem;
  border-radius: 3px;
  box-shadow: 0 0 3.2rem rgba(28, 19, 18, 0.8);
}

.editor .canvas .user-inserted-text {
  word-wrap: break-word;
}

.editor .status {
  opacity: 0;
  margin-top: 2em;
  /* temp: */
}

.editor .status.active {
  animation: notification 6s;
}

.editor a {
  color: #1c1312;
}

.editor h1 {
  line-height: 3.2rem;
}

.editor-content-saved-text {
  background: rgba(255, 255, 255, 0.9);
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 50%;
  z-index: 3;
  overflow: auto;
  box-sizing: border-box;
  padding: 2em;
}

.editor-content-saved-text .remove {
  margin-left: 1em;
}

.editor-content-saved-text .item {
  margin-bottom: 1em;
  font-size: 1.5rem;
}

.editor-content-saved-text .close {
  position: absolute;
  top: 15px;
  right: 15px;
  background: #eee;
  border-radius: 50px;
  height: 50px;
  width: 50px;
  font-size: 2em;
  text-align: center;
  line-height: 50px;
}

@keyframes notification {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 1;
  }
  60% {
    opacity: 0;
  }
}

.node-selected {
  display: inline-block;
  font-weight: bold;
  /* needed for the .close btn */
  padding: 0.4rem 3.2rem 0.4rem 2.4rem;
  border-radius: 3px;
  position: relative;
}

.node-selected.active {
  opacity: .5;
}

.node-selected.icon-comment::before, .node-selected.icon-home::before, .node-selected.icon-person::before, .node-selected.icon-tag::before {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0.4rem;
  line-height: 1.6rem;
  width: 1.6rem;
  height: 1.6rem;
  color: #1c1312;
  text-align: center;
}

.node-selected .node-close {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0.8rem;
  line-height: 1.6rem;
  width: 1.6rem;
  height: 1.6rem;
  border: 1px solid #1c1312;
  border-radius: 50%;
  font-size: 1.25rem;
  color: #1c1312;
  cursor: pointer;
  text-align: center;
}

.node-selected .node-close:hover {
  color: #1c1312;
  background: #fce4e3;
  border-color: #fce4e3;
}

.node-selected .node-close::before {
  line-height: 1.6rem;
}

.node-selected .title {
  position: absolute;
  left: -60px;
  top: 25%;
  color: #000;
  transform: rotate(-90deg);
  display: inline-block;
}

.has-comments {
  cursor: pointer;
}

.dashboard .node-selected .node-close {
  display: none;
}

.contextual-menu-component {
  font-family: "ff-meta-web-pro", Helvetica, Verdana, sans-serif;
  animation: fadeIn .25s;
  margin-bottom: 1.6rem;
  /* temp: */
  min-width: 250px;
  z-index: 2;
  padding: 0.53333rem;
  position: absolute;
  box-shadow: 0 0 0.8rem rgba(0, 0, 0, 0.2);
  background: #fdf2f2;
  border-radius: 3px;
}

.contextual-menu-component .menu-actions,
.contextual-menu-component .menu-dangerous,
.contextual-menu-component .menu-nav {
  line-height: 30px;
  font-size: 1rem;
  line-height: 2.4rem;
  text-transform: uppercase;
}

.contextual-menu-component .menu-actions {
  margin: 0;
  padding: 0;
  text-align: left;
}

.contextual-menu-component .menu-actions .item {
  display: block;
  cursor: pointer;
  padding-right: 1.6rem;
}

.contextual-menu-component .menu-actions .color:hover {
  opacity: 1;
}

.contextual-menu-component .menu-actions .command {
  opacity: .7;
}

.contextual-menu-component .menu-actions .command.with-icon.icon-comment:before, .contextual-menu-component .menu-actions .command.with-icon.icon-home:before, .contextual-menu-component .menu-actions .command.with-icon.icon-person:before, .contextual-menu-component .menu-actions .command.with-icon.icon-tag:before {
  display: inline-block;
  font-size: 1.5625rem;
  line-height: 2.4rem;
  margin-left: 0.8rem;
  margin-right: 0.8rem;
  vertical-align: middle;
}

.contextual-menu-component a.item {
  color: #1c1312;
}

.contextual-menu-component .close {
  font-size: 1.5625rem;
  position: absolute;
  top: -0.8rem;
  right: -0.8rem;
  border-bottom: 0;
}

.contextual-menu-component .close:hover {
  text-decoration: none;
  border-bottom: 0;
}

.contextual-menu-component .close:before {
  background-color: #1c1312;
  box-shadow: 0 0 0.8rem rgba(0, 0, 0, 0.2);
  color: #fce4e3;
  cursor: pointer;
  text-align: center;
  line-height: 1.6rem;
  width: 1.6rem;
  height: 1.6rem;
  border-radius: 50%;
}

.comment {
  border-radius: 15px;
  min-height: 200px;
  padding: 15px;
