.keywords-selections {
  display: flex;
  justify-content: center;
  align-items: center;

  .half-slide-previous.previous-slide & {
    // flex-flow: column;
  }

  .sources + & {
    margin-top: $base-unit * -1;
  }
}

.keywords-selection {
  padding: 0 $base-unit * .25 $base-unit;
  background-color: $bg-color-2;
  color: $bg-color;
}

.keywords-selection-items-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.keywords-selection-title {
  color: currentColor;
}

.keyword-item {
  position: relative;
  flex: 0 1 auto;
  list-style: none;
  box-shadow: 0 0 $base-unit * .25 rgba($text-color, 0.38);
  background-color: $bg-color;
  border-radius: $base-border-radius;
  color: $text-color;
  margin-bottom: $base-unit * .5;
  margin-right: $base-unit * .5;
  padding: $base-unit * .25 $base-unit * .75;
  padding-left: $base-unit * 2;
  line-height: ms-line-height(2);
  font-size: ms(2);
  text-align: left;
  cursor: pointer;

  .half-slide-previous.previous-slide & {
    font-size: ms(0);
    line-height: ms-line-height(0);
  }

  &:hover {
    opacity: .62;
  }

  &.disabled {
    opacity: .38;
    box-shadow: none;

    &:before {
      content: "x";
    }

    &:hover {
      opacity: .62;
      box-shadow: 0 0 $base-unit * .25 rgba($text-color, 0.38);
    }
  }

  &:before {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: $base-unit * .75;
    color: $bg-color-2;
    font-weight: bold;
  }
}

// JI: Kde je .keyboard-list?
.keywords-list {
  display: inline-block;
  box-sizing: border-box;
  padding: .75rem 1.5rem 0;
  border-right: 1px solid rgba(0,0,0,.1);
  border-bottom: 1px solid rgba(0,0,0,.1);
  flex: 1 0 250px;

  &:first-of-type {
    border-left: 1px solid rgba(0,0,0,.1);
  }

  label {
    display: block;
    margin-bottom: 1rem;
  }

  .select2-container {
    margin: .75rem 0;
  }
}
