.captions,
.popisky {
  justify-content: space-between;
  padding: 0 0 $base-unit * 2;
  margin-top: $base-unit * -2;
  background-color: $bg-color-2;
  color: $base-color;
  font-size: ms(-1);
  line-height: ms-line-height(-1);
}

.caption,
.popisek {
  padding: 0 $base-unit * .25;
  // flex: 1;

  p {
    display: inline-block;
    text-align: left;
    max-width: $base-unit * 20;
    margin-left: auto;
    margin-right: auto;
  }
}
