#horakova-rezoluce-2.previous-to-active-slide {
  .sources .item:first-child {
    display: none;
  }

  .editor .contextual-menu-component[style] {
    display: none !important;
  }

  .editor .node-close {
    display: none;
  }

  .item.caption.popisek {
    display: none;
  }

}

/* :temp: */

.editor.editor-editable .canvas {
  background: #f1e0c4;
  box-shadow: inset 0 0 12px rgba(0,0,0,.9);
  font-family: monospace;
  font-size: 20px;
  line-height: 1.8em;
  text-indent: 135px;
}

.editor {
  width: 80%;
  max-width: 880px;
}

.half-slide-active.active-slide,
.previous-slide {
  .editor {
    width: auto;
  }
}

.user-inserted-text {
  background-color: #fdf2f2;
  color: rgb(31, 161, 93);
  border-top: 2px solid rgb(31, 161, 93);
  border-bottom: 2px solid rgb(31, 161, 93);
}

.user-removed-text {
  color: red;
  text-decoration: line-through;
  border-top: 2px solid red;
  border-bottom: 2px solid red;
}

.user-removed-text-inner {
  color: #000;
}

.editor .editor-toolbar {

  height: 55px;
  margin-top: -55px;
  text-align: right;

  .editor-toolbar-actions {
    display: inline-block;
    position: relative;
    top: -1em;
  }

  .editor-toolbar-action-reset {
    padding: 1em 2em;
    border-radius: 3px;
    background-color: #a77977;
    color: #fce4e3;
    display: inline-block;
    border: 1px solid #fff;
    cursor: pointer;
  }
}

/* :end temp: */
