$borderSettings: 1px solid desaturate(darken($bg-color, 10), 30);

.table.table-column {width: 25%; display: inline-block; margin-right: 5%;}

.table.table-selected-items {
  // margin-bottom: $base-unit;
  min-height: $base-unit * 4;
  border-top-left-radius: $base-border-radius;
  border-top-right-radius: $base-border-radius;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;

  .table-body {
    padding: $base-unit / 2;
  }

  .node-selected {
    cursor: pointer;

    .node-close {
      display: none;
    }
  }

}

.table {
  width: 70%;
  margin: 0 auto;
  background: $bg-color-1;
  overflow: hidden;
  min-height: 300px;
  border-bottom-left-radius: $base-border-radius;
  border-bottom-right-radius: $base-border-radius;
  // box-shadow: $box-shadow;

  .node-selected {
    line-height: ms-line-height(1);
    // padding: $base-unit / 4 $base-unit * 2 $base-unit / 4 $base-unit * 1.5;
    margin: $base-unit / 8;
    cursor: default;
    border: none;
  }

  .table-row {
    background: $bg-color-1;
    border: $borderSettings;
    border-top: 0;
    overflow: hidden;
  }

  .cell {
    box-sizing: border-box;
    min-height: $base-unit * 3;
    line-height: $base-unit * 3;
  }

  .cell-read-only .node-close {display: none;}

  .cell-border-right {
      border-right: $borderSettings;
  }

  &.table-2 {
    .cell {
      width: 50%;
    }

  }

  &.table-3 {
    .cell {
      width: 33.3333%;
    }
  }

  .table-header {
    .heading {
      // padding: 2px 5px;
      background: desaturate(darken($bg-color, 10), 30);
      min-height: $base-unit * 3;
      line-height: $base-unit * 3;
      // font-size: ms(0);
      // font-weight: bold;
      // text-transform: uppercase;
    }

  }

  input[type="text"] {
    width: 100%;
    min-height: $base-unit * 3;
    padding: 0 $base-unit / 2;
    border: 0;
  }

}

.table-2 .cell,
.table-3 .cell {
  float: left;
}
