.slides {
  width: 100vw;
  height: 100%;
}

.slide {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: calc(100% - #{$navigation-height});
  padding: $base-unit * 3 0 0;
  transform: translate3d(0, 0, 0);
  will-change: transform, width;
  transition: transform .38s, width .38s;
  overflow-x: hidden;
  overflow-y: scroll;
  text-align: center;

  &.previous-slide {
    transform: translate3d(-100vw, 0, 0);

    &.half-slide-active,
    &.half-slide-previous {
      width: 50%;
    }

    &.half-slide-active {
      transform: translate3d(-50vw, 0, 0);
    }

    &.half-slide-previous {
      transform: translate3d(-100vw, 0, 0);
      border-right: 1px solid lighten($bg-color, 3);
    }
  }

  &.previous-to-active-slide {
    &.half-slide-previous {
      width: 50%;
      transform: translate3d(0, 0, 0);
    }
  }

  &.active-slide {
    width: 100%;

    &.half-slide-active {
      width: 50%;
      transform: translate3d(50vw, 0, 0);
    }
  }

  &.next-slide {
    transform: translate3d(100vw, 0, 0);

    &.half-slide-active {
      width: 50%;
      transform: translate3d(100vw, 0, 0);
    }
  }
  @for $i from 1 through 10 {
    $bg-color: darken(#f2f2f2, $i * $i);

    .slide:nth-child(#{$i}) {
      background: $bg-color;
    }
  }
}

.row {
  display: flex;
  align-items: stretch;
  justify-content: center;
  // margin-bottom: $base-unit;
  &:last-child {
    padding-bottom: $base-unit * 3;
    margin-bottom: 0;
  }
}

.item {
  flex: 1;
  flex: 1 1 0;
}

.prameny,
.sources,
.items {
  align-items: flex-start;
  justify-content: space-between;
  background: $bg-color-2;
  padding-bottom: $base-unit * 3;
  line-height: 0;
}

.pramen,
.source {
  flex: 3 1 0;
  padding: 0 $base-unit / 4;

  image,
  img {
    max-width: 100%;
  }

   > img,
  > .svg {
    max-height: 80vh;
    box-shadow: $box-shadow;
    border-radius: $base-border-radius;
  }
}

.column {
  &.half-slide-previous.previous-slide {
    .sources {
      flex-flow: column;
    }

    .source {
      margin: 0 auto $base-unit;
    }
  }
}
