$help-size: $base-unit * 2;

.help-container {
  padding: $base-unit;
  line-height: 0;
  background-color: $bg-color-2;
  color: $bg-color;

  //
  @if $theme == light {
    color: $text-color;
  }
}

.help-activities {
  display: flex;
  justify-content: center;
  position: relative;
}

.help-activity {
  flex: 0 0 auto;
  width: $help-size;
  height: $help-size;
  margin: 0 $base-unit * .125;
  border: 2px solid;
  border-radius: 50%;
  overflow: hidden;
  box-sizing: content-box;

  .icon-svg {
    width: $help-size;
    height: $help-size;
  }
}

.help-text {
  display: none;
  position: absolute;
  top: calc(100% + #{$base-unit / 2});
  left: 50%;
  transform: translateX(-50%);
  width: 38vw;
  max-width: 600px;
  padding: $base-unit;
  border-radius: $base-border-radius;
  font-size: ms(-1);
  line-height: ms-line-height(-1);
  background-color: $text-color;
  box-shadow: $box-shadow;
  z-index: 999;

  .help-activities:hover & {
    display: block;
  }

  &::after {
    content: " ";
    position: absolute;
    bottom: 100%;
    left: 50%;
    border: solid transparent;
    height: 0;
    width: 0;
    // pointer-events: none;
    border-color: rgba($text-color, 0);
    border-bottom-color: $text-color;
    border-width: $base-unit;
    margin-left: -$base-unit;
  }

  h6 {
    color: $bg-color;
    text-transform: uppercase;
  }
}
