#loading-screen {
  display: none;
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  background-color: #222;
  color: $bg-color;
  text-transform: lowercase;
  z-index: 9999999;

  will-change: opacity;
  opacity: 0;
  transition: opacity .62s .62s $timing-in;

  .loading & {
    pointer-events: initial;
    opacity: 1;

    h1 {
      opacity: 1;
    }

    svg {
      opacity: 1;
      transform: scale3d(1,1,1);
    }
  }

  h1,
  svg {
    color: $bg-color;
    display: block;
  }

  svg {
    margin: 0 auto;
    width: $base-unit * 5;
    height: $base-unit * 5;
    fill: currentColor;
  }

  h1 {
    margin-bottom: $base-unit * 2;
  }
}

.loading-screen-content {
  text-align: center;
  opacity: 0;
  will-change: opacity, transform;
  transform: scale3d(1.2, 1.2, 1.2);
  transition: opacity .38s .38s $timing-in, transform .38s .38s $timing-in;

  .loading & {
    opacity: 1;
    transform: scale3d(1, 1, 1);
  }
}

