.contextual-menu-component {
  font-family: $base-font-family;
  animation: fadeIn .25s;

  margin-bottom: $base-unit;
  /* temp: */
  min-width: 250px;
  z-index: 2;
  padding: $base-unit / 3;
  position: absolute;
  box-shadow: $button-shadow;
  background: $bg-color-1;
  border-radius: $base-border-radius;

  .menu-actions,
  .menu-dangerous,
  .menu-nav {
    line-height: 30px;
    font-size: ms(0);
    line-height: ms-line-height(2);
    text-transform: uppercase;
  }

  .menu-actions {
    margin: 0;
    padding: 0;
    text-align: left;

    .item {
      display: block;
      cursor: pointer;
      padding-right: $base-unit;
    }

    .color {
      &:hover {
        opacity: 1;
      }
    }

    .command {
      opacity: .7;

      &.with-icon {
        &.icon-tag:before {
          @extend .ion-ios-pricetag-outline !optional;
        }

        &.icon-person:before {
          @extend .ion-ios-person-outline !optional;
        }

        &.icon-home:before {
          @extend .ion-ios-home-outline !optional;
        }

        &.icon-comment:before {
          @extend .ion-ios-paper-outline !optional;
        }

        &.icon-comment,
        &.icon-home,
        &.icon-person,
        &.icon-tag {
          &:before {
            display: inline-block;
            font-size: ms(2);
            line-height: ms-line-height(2);
            margin-left: $base-unit / 2;
            margin-right: $base-unit / 2;
            vertical-align: middle;
          }
        }

      }

    }


  }

  a.item {
    color: $text-color;
  }

  .close {
    font-size: ms(2);
    position: absolute;
    top: $base-unit * -0.5;
    right: $base-unit * -0.5;
    border-bottom: 0;

    &:hover {
      text-decoration: none;
      border-bottom: 0;
    }

    &:before {
      @extend .ion-ios-close-outline !optional;

      background-color: $text-color;
      box-shadow: $button-shadow;
      color: $bg-color;
      cursor: pointer;
      text-align: center;
      line-height: $base-unit;
      width: $base-unit;
      height: $base-unit;
      border-radius: 50%;
    }
  }

}
