#export {
  .row {
    align-items: center;
  }

  .form {}

  p {
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }

  fieldset {
    padding: $base-unit;
    background-color: $bg-color-1;
    margin-bottom: $base-unit;
    border: 0;

    &:nth-of-type(1) {
      text-align: left;

      label {
        display: block;
      }
    }

    &:nth-of-type(2) {
      text-align: center;

      input,
      label {
        cursor: pointer;
      }

      label {
        display: inline-block;
        padding: 0 $base-unit;
        border-radius: $base-border-radius;

        &:hover {
          // background-color: $bg-color;
          font-weight: 700;
        }
      }
    }
  }

  legend {
    font-weight: 700;
  }

  label {
    margin-bottom: $base-unit / 8;
    font-size: ms(-1);
    line-height: ms-line-height(-1);
  }

  input {
    background-color: lighten($bg-color-1, 2);
  }

  input[type="email"],
  input[type="text"] {
    display: block;
    line-height: $base-line-height;
    padding: $base-unit / 4 $base-unit / 2;
    border-radius: $base-border-radius;
    border: 1px solid $bg-color-2;
    width: 100%;

    margin-bottom: $base-unit;

    &:last-child {
      margin-bottom: 0;
    }

    &::-webkit-input-placeholder {
      /* Chrome/Opera/Safari */
      color: rgba($text-color, .38);
    }

    &::-moz-placeholder {
      /* Firefox 19+ */
      color: rgba($text-color, .38);
    }

    &:-ms-input-placeholder {
      /* IE 10+ */
      color: rgba($text-color, .38);
    }

    &:-moz-placeholder {
      /* Firefox 18- */
      color: rgba($text-color, .38);
    }
  }

  input[type="submit"] {
    font-weight: 700;
    padding: $base-unit $base-unit * 2;
    margin: 0 $base-unit $base-unit;
    border-radius: $base-border-radius;
    border: 1px solid $text-color;
    background-color: $bg-color-2;
    // color: $bg-color;
    cursor: pointer;

    &:hover {
      background-color: lighten($bg-color-2, 10);
      box-shadow: $button-shadow;
      // border-color: $bg-color-2;
      color: $text-color;
    }
  }

  .explanation-text {
    font-size: ms(-1);
    line-height: ms-line-height(-1);
    font-style: italic;
  }
}
