.user-texts,
.uzivatelske-texty {
  padding-bottom: $base-unit * 2;
  margin-bottom: 0;
  background-color: $text-color;
  background-color: $bg-color-2;
  color: $bg-color;

  display: flex;
  justify-content: center;
  align-items: center;

  @if $theme == light {
    color: $text-color;
  }

  .column & {
    flex-direction: column;
  }

  .captions + &,
  .sources + & {
    // border-top: 1px solid rgba($bg-color, .38);
    // padding-top: $base-unit;
    margin-top: $base-unit * -1;
  }

  h3 {
    color: currentColor;
    max-width: 30em;
    margin-left: auto;
    margin-right: auto;
  }

  textarea {
    max-width: 30em;
    width: 100%;
    width: 30vw;
    height: auto;
    padding: $base-unit;
    border: 1px solid $bg-color;
    resize: vertical;
    border-radius: $base-border-radius;

    transition: width .38s;

    .half-slide-previous.previous-slide:not(.column) & {
      width: (30vw / 2);
    }
  }
}

.user-text,
.uzivatelsky-text {
  margin-bottom: $base-unit;
}
