.sortable {
  counter-reset: section;

  &.is-dragged {
    cursor: -moz-grabbing;
    cursor: -webkit-grabbing;
    cursor: grabbing;
  }

  img {
    user-drag: none;
    user-select: none;
    pointer-events: none;
  }
}

.sortable-item {
  flex: 1;
}

.sortable-drag-item {
  position: relative;
  cursor: -moz-grab;
  cursor: -webkit-grab;
  cursor: grab;

  transition: transform $base-duration $timing-out;

  &.is-dragged,
  &:hover {
    transform: scale(1.038);
  }

  &.is-dragged {
    position: absolute;
    z-index: 1;
    cursor: -moz-grabbing !important;
    cursor: -webkit-grabbing !important;
    cursor: grabbing !important;
  }

  .is-dragged & {
    cursor: -moz-grabbing;
    cursor: -webkit-grabbing;
    cursor: grabbing;
  }

  * {
    pointer-events: none;
  }
}

.sortable-drop-item {
  &::before {
    counter-increment: section;
    content: counter(section) ". ";
    position: absolute;
    top: $base-unit / -2;
    left: $base-unit / 4;
    color: rgba($bg-color, .62);
  }

  .is-dragged & {
    transform: scale(.9);

    &.is-dragged {
      transform: scale(1.038) rotate(5deg);
    }
  }
}

.sortable-placeholder {
  transform: scale(.9);
  padding: 0;
  overflow: hidden;
  border: 1px dashed darken(#f7f7f7, 15);
  border-radius: $base-unit;
  background-color: $text-color;

  img {
    border-radius: $base-unit;
    opacity: .38;
  }
}
