.faksimile-container {
  position: relative;
  flex: 0 1 38%;
  // max-height: 90vh;
  overflow-x: hidden;
  overflow-y: scroll;
  margin-left: $base-unit;
  box-shadow: $box-shadow;
  border-radius: $base-border-radius;
  counter-reset: faksimile;

  &.faksimile-single {
    .faksimile-item::before {
      display: none;
    }
  }

  &.faksimile-multiple {
    max-height: 90vh;
  }

  .active-slide & {
    min-width: 38%;
  }

  .half-slide-previous.previous-to-active-slide & {
    display: none;
  }
}

.faksimile-item {
  position: relative;

  &::before {
    counter-increment: faksimile;
    content: counter(faksimile);
    position: absolute;
    top: $base-unit;
    left: $base-unit;
    width: $base-unit;
    height: $base-unit;
    line-height: $base-unit;
    background-color: rgba($text-color, .62);
    border-radius: 50%;
    box-shadow: 0 0 $base-unit / 2 rgba($text-color, .62);
    color: $bg-color;
    font-family: $ui-font-family;
    font-feature-settings: "lnum";
    font-size: ms(-1);

    will-change: opacity;
    transition: opacity $base-duration $base-timing;
  }

  &:hover {
    &::before {
      opacity: .38;
    }
  }

  img {
    width: 100%;
  }
}
