/* Horizontln navigace */
#navigation {
  z-index: 99999;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  min-height: $navigation-height;
  background-color: $text-color;
  padding: 0;
  color: $bg-color;
  text-align: center;
  line-height: $navigation-height;
  border-top: 1px solid rgba(lightgray,.2);

  display: flex;
}

.nav-button {
  flex: 0 1 auto;
  position: relative;
  padding: 0 $base-unit;
  cursor: pointer;
  color: $bg-color;
  border-bottom: 0;
  text-transform: uppercase;
  font-size: ms(-1);

  &:hover {
    span {
      color: $bg-color;
      // opacity: 1;
    }
  }

  &.is-hidden {
    pointer-events: none;
    visibility: hidden;
  }

  span {
      color: $bg-color-2;
    // opacity: .38;
    will-change: color;
    transition: color $base-duration $base-timing;
  }

  .icon-svg {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: $base-unit;
    height: $base-unit;
    color: $bg-color;
  }
}

.nav-center {
  flex: 1;
}

.nav-button-prev {
  border-right: 1px solid rgba(lightgray,.2);
  text-align: left;
  padding-left: $base-unit * 2;

  .icon-svg {
    left: $base-unit / 4;
    transform: rotate(180deg) translateY(50%);
  }
}

.nav-button-next {
  border-left: 1px solid rgba(lightgray,.2);
  text-align: right;
  padding-right: $base-unit * 2;

  .icon-svg {
    right: $base-unit / 4;
  }
}
