.node-selected {
  display: inline-block;
  font-weight: bold;
  /* needed for the .close btn */
  padding: $base-unit / 4 $base-unit * 2 $base-unit / 4 $base-unit * 1.5;
  border-radius: $base-border-radius;
  position: relative;

  &.active {
    opacity: .5;
  }

  &.icon-tag:before {
    @extend .ion-ios-pricetag-outline !optional;
  }

  &.icon-person:before {
    @extend .ion-ios-person-outline !optional;
  }

  &.icon-home:before {
    @extend .ion-ios-home-outline !optional;
  }

  &.icon-comment:before {
    @extend .ion-ios-paper-outline !optional;
  }

  &.icon-comment,
  &.icon-home,
  &.icon-person,
  &.icon-tag {
    &::before {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: $base-unit / 4;
      line-height: $base-unit;
      width: $base-unit;
      height: $base-unit;
      color: $text-color;
      text-align: center;
    }
  }

  .node-close {
    @extend .ion-ios-close-outline !optional;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: $base-unit / 2;
    line-height: $base-unit;
    width: $base-unit;
    height: $base-unit;
    border: 1px solid $text-color;
    border-radius: 50%;
    font-size: ms(1);
    color: $text-color;
    cursor: pointer;
    text-align: center;

    &:hover {
      color: $text-color;
      background: $bg-color;
      border-color: $bg-color;
    }

    &::before {
      line-height: $base-unit;
    }
  }

  // JI: Co je a kde se vyskytuje .status?
  .title {
    position: absolute;
    left: -60px;
    top: 25%;
    color: #000;
    transform: rotate(-90deg);
    display: inline-block;
  }
}

.has-comments {
  cursor: pointer;
}

.node-text-red {
  @extend .text-red !optional;
}

.node-text-blue {
  @extend .text-blue !optional;
}

.node-text-gray {
  @extend .text-gray !optional;
}

.node-text-orange {
  @extend .text-orange !optional;
}

.node-text-yellow {
  @extend .text-yellow !optional;
}

.dashboard .node-selected .node-close {
  display: none;
}
